.header{
     display: flex;
     justify-content: space-between;  
     align-items: center;
     background-color: white;
     box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);    z-index: -1;
     top: 0;
    

      
}
.logo{
     width: 100px;
     margin-top: 0;
     background-color: black;
}
.nav-items>ul>li{
     
   margin-right: 40px;
   padding-left: 0px;
   font-weight: bold;

}
.nav-items>ul{
     display: flex;
     list-style: none;
     margin-right: 30px;


}
.res-container{
     flex-wrap: wrap;
     display: flex;
     margin-top: 10px;
     
}
.full{
     padding-left: 50px;

}
.res-card{
     margin: 10%;
     width: 200px;
     height: 300px;
     box-shadow: 9px 9px 0px rgba(228, 225, 225, 0.6); 
     border-radius: 10px;
}
.res-card:hover{
     box-shadow: 2px 2px 10px rgba(222, 24, 24, 0.5);
     cursor: pointer; 
     
}
.search{
  padding: 20px;
}

.res-img {
     padding: 5px;
     width: 100%;
     height: 140px;
     padding-left: 0px;
     padding-top: 0px;
   }
   
   .items {
     font-size: medium;
   }
   
   .items h3 {
     margin-top: 0;
     margin-bottom: 5px; /* Add some spacing below the heading */
   }
   
   .items .cuisines {
     margin-top: 5px; /* Add spacing between cuisines and other details */
   }
   
   .items p {
     margin-top: 5px; /* Add spacing between each item */
   }
   
   .items h4 {
     margin-bottom: 0px;
     margin-top: 15px;
     
   }
   